<template>
  <div class="password-wrapper">
    <form action ref="form-wrapper" class="form-wrapper">
      <div class="input-wrapper">
        <div class="icon">
          <svg-icon icon-class="user" />
        </div>
        <div class="text-input">
          <input type="text" v-model="loginForm.userName" placeholder="手机号/用户编号/邮箱" />
        </div>
      </div>
      <div class="input-wrapper bottom-mag">
        <div class="icon">
          <svg-icon icon-class="password" />
        </div>
        <div class="text-input">
          <input :type="pwdType" v-model="loginForm.password" placeholder="请输入您的密码" />
        </div>
        <div class="icon-right">
          <svg-icon icon-class="eye" @click="showPwd" />
        </div>
      </div>
      <div class="help-wrapper">
        <div class="right-wrapper" @click="retrieve">忘记密码</div>
      </div>
      <div class="submit-wrapper">
        <button type="submit" @click.prevent="submitForm" class="submit">登录</button>
      </div>
    </form>
    <!-- <van-overlay :show="show" @click="show=false">
      <div class="wrapper">
        <div class="block" @click.stop>
          <div class="title-wrapper">请选择密码找回方式</div>
          <div class="content-wrapper">
            <router-link tag="div" class="link-wrapper" to="../retrieve/phone-way">
              <div class="text">手机号找回</div>
              <div class="icon">
                <van-icon name="arrow" />
              </div>
            </router-link>
            <router-link tag="div" class="link-wrapper" to="../retrieve/email-way">
              <div class="text">邮箱找回</div>
              <div class="icon">
                <van-icon name="arrow" />
              </div>
            </router-link>
            <router-link tag="div" class="link-wrapper" to="../retrieve/artificial-way">
              <div class="text">人工找回</div>
              <div class="icon">
                <van-icon name="arrow" />
              </div>
            </router-link>
          </div>
        </div>
      </div>
    </van-overlay> -->
  </div>
</template>

<script>
import { setEncryption } from "@/mixins/user";
import { isUserName, isPassWord } from "@/utils/validate";
export default {
  mixins: [setEncryption],
  data() {
    return {
      // show: false,
      loginForm: {
        userName: "",
        password: "",
      },
      pwdType: "password",
    };
  },
  methods: {
    showPwd() {
      if (this.pwdType === "password") {
        this.pwdType = "";
      } else {
        this.pwdType = "password";
      }
    },
    async rules() {
      if (this.loginForm.userName !== "") {
        if (!isUserName(this.loginForm.userName)) {
          this.$toast("用户名不符合规则");
          return false;
        }
      } else {
        this.$toast("请输入用户名");
        return false;
      }
      if (this.loginForm.password !== "") {
        if (!isPassWord(this.loginForm.password)) {
          this.$toast("密码不符合规则");
          return false;
        }
      } else {
        this.$toast("请输入密码");
        return false;
      }
      return true;
    },
    submitForm(event) {
      this.$reCaptcha(event, "passLogin");
      let _this = this;
      this.rules().then((valid) => {
        if (valid) {
          _this.$store
            .dispatch("login_password", {
              userName: this.loginForm.userName,
              password: this.encryption(this.loginForm.password),
            })
            .then((res) => {
              console.log(res);
            })
            .catch(error => {
              _this.$router.push({ name: 'm_account' })
            });
        }
      });
    },
    retrieve(){
      this.$router.push({name:'m_retrieve'})
    }
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/styles/mobile/index.scss";
.password-wrapper {
  width: 100%;
  height: 100%;
  .form-wrapper {
    width: 100%;
    padding: 40px;
    box-sizing: border-box;
    .help-wrapper {
      padding-top: 10px;
      box-sizing: border-box;
      width: 100%;
      font-size: 13px;
      text-align: right;
      color: #999999;
      margin-bottom: 30px;
    }
    .text-input {
      padding: 0 10px 0 10px;
    }
  }
}
</style>


